// app/api/revalidate/route.js
import { revalidatePath } from "next/cache";
import { NextResponse } from "next/server";

export async function POST(request) {
  try {
    const { password } = await request.json();

    // Simple password check (use same as admin panel)
    if (password !== "admin123") {
      return NextResponse.json(
        { message: "Invalid password" },
        { status: 401 }
      );
    }

    // Revalidate all collection pages
    revalidatePath("/rings");
    revalidatePath("/earrings");
    revalidatePath("/necklaces");
    revalidatePath("/bracelets");
    revalidatePath("/search");

    return NextResponse.json({
      revalidated: true,
      message: "All product pages have been updated with new pricing!",
      timestamp: new Date().toISOString(),
    });
  } catch (error) {
    return NextResponse.json(
      { message: "Error revalidating", error: error.message },
      { status: 500 }
    );
  }
}
